$jet: #2c302eff;
$jet-transparent: #2c302ecf;
$outer-space: #474a48ff;
$outer-space-transparent: #474a48cf;
$battleship-gray: #909590ff;
$battleship-gray-transparent: #909590cf;
$celadon: #9ae19dff;
$fern-green: #537a5aff;

.App {
  text-align: center;
  background-color: $jet;
  color: white;
  height: 100%;
  user-select: none;
}

body{
  height: max-content;
  background-color: $fern-green;
  font-size: larger;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

#root{
  height: 100%;
}

.title-bar {
  height: 10%;
}

.content-field {
  height: 90%;
}

.MuiMenu-list {
  background-color: $outer-space;
}

.editor-header {
  z-index: 1000;
  background-image: linear-gradient($jet, $jet-transparent);
}

.overlaid-footer {
  background-image: linear-gradient($jet-transparent, $jet);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.dialog-content {
  background: $battleship-gray;
}

.base-modal {
  background-color: $outer-space;
}

.step-navigation{
  display: flex;
  padding-top: 10px;
  justify-content: space-between;
}

.navbar-item {
  width: 150px;
  background: $jet-transparent;
  box-shadow: 5px 5px 10px $jet;
}

.navbar-item:hover {
  background: $outer-space-transparent;
}

.ensemble-card:hover {
  background: $outer-space-transparent;
}

#navlist {
  transform: translate(-300px, 0);
  list-style: none;
  z-index: 2000;
}

#navlist.open {
  transform: translate(0, 0);
}

.text-shadow {
  text-shadow: 0px 0px 10px $jet;
}

.text-container{
  text-align: left;
  padding: 20px;
  text-align: justify;
}

li.active .navbar-item{
  background: $battleship-gray-transparent;
}

.selected-ensemble{
  background: $battleship-gray;
}

.nav-toggle {
  z-index: 2000;
  width: 44px;
  cursor: pointer;
  position: absolute;
  top: 21px;
}

.nav-toggle.active {
  background: $battleship-gray;
}

@media (min-width: 768px){
  nav #navlist {
    transform: translate(0, 0) !important;
  }
}
